class NavScroll {
    constructor (selector) {
      this.$element = document.querySelector(selector)
      this.navActiveClass =  this.$element.dataset.activeClass //amplify-nav
      this.navInitClass =  this.$element.dataset.initClass //'is-transparent'
  
      document.addEventListener('DOMContentLoaded', () => {
        if (typeof this.callback === 'function') {
          this.callback()
        }
      })
    }
  
    onReady (callback) {
      this.callback = callback
    }
    init () {
      let navbar = this
  
      if (window.scrollY > 1) {
        navbar.$element.classList.remove(navbar.navInitClass)
        navbar.$element.classList.add(navbar.navActiveClass)
      }
  
      window.addEventListener('scroll', function () {
        let scroll = window.scrollY
        if (scroll > 1) {
          navbar.$element.classList.remove(navbar.navInitClass)
          navbar.$element.classList.add(navbar.navActiveClass)
        } else {
          navbar.reset()
        }
      })
    }
    reset () {
      this.$element.classList.remove(this.navActiveClass)
      this.$element.classList.add(this.navInitClass)
    }
  }
  
  export default NavScroll
  