import Vue from 'vue'
import App from './App.vue'
import MobileMenu from './scripts/MobileMenu'
import NavScroll from './scripts/NavScroll'

Vue.config.productionTip = false

new Vue({
  render: h => h(App),
}).$mount('#app')

// mobile menu
var mobileMenu = new MobileMenu('#navbarMain')
mobileMenu.onReady(() => { mobileMenu.init() })

// navbar main
var navScroll = new NavScroll('#navbarMain')
navScroll.onReady(() => { navScroll.init() })

document.addEventListener('DOMContentLoaded', () => {
  console.log('Amplify - Umbraco Premium Theme - made with LOVE.')
})
